.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.calendar {
  width: $calendar-size;
  padding: $calendar-padding;
  display: flex;
  flex-wrap: wrap;
  gap: $calendar__day-gap;

  $week:
    'mon' 0,
    'tue' 1,
    'wed' 2,
    'thu' 3,
    'fri' 4,
    'sat' 5,
    'sun' 6;

  @each $day, $number in $week {
    &--start-day_#{$day} &__day:first-child {
      margin-left: ($calendar__day-size + $calendar__day-gap) * $number;
    }
  }

  @for $last-day from 28 through 31 {
    &--month-length_#{$last-day} &__day:nth-child(n + #{$last-day + 1}) {
      display: none;
    }
  }

  &__day {
    @include square($calendar__day-size);
    box-sizing: border-box;
    border: 1px solid black;
    background-color: #eee;

    &:hover {
      cursor: pointer;
      background-color: pink;
      transform: translateY(-20px);
    }

    @for $index from 1 through 31 {
      &:nth-child(#{$index})::before {
        content: '#{$index}';
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }
}
